body {
	margin: 0;
	padding: 0;
	background-color: #161616;
	font-family: "Noto Sans KR", sans-serif;
}
.loading {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
}
.loading img {
	width: 60px;
}
.wrap {
	display: flex;
	flex-wrap: wrap;
	padding: 15px;
}
